/* SCSS Document */

@charset "utf-8";
@import "../../object/component/_color";
@import "../../object/component/_font";
@import "../../object/component/_grid";
.o-maintitle {
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
    & .o-title--lg{
        line-height: 24px;
    }
    &__update {
        margin-left: auto;
        & .btn {
            font-size: $font12;
            background-color: $puregrey;
            cursor: pointer;
            font-weight: bold;
            padding: .5rem 0;
        }
        & .dropdown-toggle-extend:after {
            display: inline-block;
            width: 0;
            height: 0;
            margin-left: .3em;
            vertical-align: middle;
            content: "";
            border-top: .5em solid;
            border-right: .5em solid transparent;
            border-left: .5em solid transparent;
        }
    }
    &>.o-maintitle__midoku {
        margin-left: 16px;
        background-color: $red;
        width: 70px;
    }
}


