/* お使いの製品 */

.o-productlist {
    .o-rowcontainer {
        padding: 24px;
        min-height: 86px;
        border-top: 1px solid rgba(0, 0, 0, 0.12);
        transition-duration: 0.28s;
        transition-property: background-color;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        // cursor: pointer;
        &:hover{
            background-color: #eeeeee;
        }
//        min-height: 120px;
        .o-producticon {
            margin: 0px 5px;
        }
        .o-productdetail {
            margin: 0px 5px 0px 29px;
            min-width: 290px;
            div.o-productdetail__row--top {
                .o-productname {
                    font-size: $font17;
                    margin: 0px 5px;
                }
                .o-productplan {
                    font-size: $font13;
                    color: $darkgrey;
                    margin: 0px 5px;
                }
            }
            div.o-productdetail__row--center {
                margin: 5px 5px;
            }
            div.o-productdetail__row--bottom {
                flex-basis: auto;
                font-size: $font13;
                margin: 0px 5px;
                & > a:hover{
                    cursor: pointer;
                }
            }
        }
        .o-productstart {
            margin-left: auto;
            &>.mdl-button {
                color: $white;
            }
            & >.mdl-button:hover {
                color: $black;
            }            
        }
    }
    //--flexboxの指定 start --
    .o-rowcontainer {
        display: flex;
        flex-direction: row;
        //        justify-content: flex-start;
        align-items: center;
        flex-wrap: wrap;
        .o-producticon {
            flex-grow: 0;
            flex-shrink: 0;
            &>img {
                width: 167px;
            }
        }
        .o-productdetail {
            flex-grow: 1;
            justify-content: flex-start;
            div.o-productdetail__row--top {
                display: flex;
                flex-direction: row;
                flex: 1;
                flex-basis: auto;
            }
            div.o-productdetail__row--center,
            div.o-productdetail__row--bottom {
                flex: 1;
                flex-basis: auto;
            }
        }
    }
    //--flexboxの指定 end --
}


/* 弥生からのお知らせ */

.o-noticelist {
    .mdl-data-table {
        white-space: normal;
    }
    & .is-detailexists{
        transition: height 0.3s ease;
        height: 59px;
        cursor: pointer;
        overflow-y: hidden;
    }  

    & div[data-message-read-flag="1"] .o-notification a {
        color:$lightgrey--light;
        &:hover{
            color: $blue--dark;
        }
    }
    .o-rowcontainer {
        min-height: 34px;
        border-top: 1px solid rgba(0, 0, 0, 0.12);
        transition-duration: 0.28s;
        transition-property: background-color;
        transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
        padding: 12px 24px;
        &:hover{
            background-color: #eeeeee;
        }
        .o-tagnotice {
            &--important {
                margin: 0px 5px;
                background-color: $red;
            }
            &--info {
                margin: 0px 5px;
                background-color: $darkgrey;
            }
        }
        .o-notification {
            font-size: $font15;
            margin: 0px 5px;
            display: flex;
            align-items: center;
            white-space: normal;
            overflow: hidden;
            width: 100%;
            
            & span{
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;     
            }
            
            & i {
                margin-left: 4px;
                color: $yblue;
			}

            & a {
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
        }
        .o-updatedate {
            color: $darkgrey;
            margin: 0px 12px 0px 8px;
        }
        .o-menuicon {
            font-size: $font22;
            margin: 0px 5px;
        }
    }
    & div:not(.is-detailexists) .o-rowcontainer .o-updatedate{
        margin-right: 45px;
    }     
    .o-opendetail.mdl-button--icon {
        border-style: solid;
        border-width: 2px;
        border-color: $yblue;
        background-color: $yblue;
        // position: absolute;
        // top: 15px;
        // right: 15px;
        & i{
            font-size: $font17;
            top: 50%;
            color: $white;
        }
        &:hover{
            background-color: $white;
            & i{
                color: $yblue;
            }                
        }
    }    
    .o-rowcontainer:not(.is-read) {
        & .o-notification,
        & .o-updatedate {
            font-weight: bold;
        }
    }    
    .o-rowcontainer.is-read {
        & .o-tag--rad4 {
            background-color: $lightgrey--light;
        }
        & .o-notification,
        & .o-updatedate {
            color: $lightgrey--light;
        }
    }
    .o-rowcontainer__detail {
        padding-left: 82px;
        padding-right: 62.65px;
        background-color: $yellow--light;
        .o-rowcontainerdetail__message {
            text-align: left;
            padding: 16px;
            color: $darkgrey;
            max-width: 880px;
            white-space: normal;
            & a {
                text-decoration: underline;
            }
        }
    }

    //--flexboxの指定 start --
    .o-rowcontainer {
        display: flex;
        flex-direction: row;
//        justify-content: flex-end;
        align-items: center;
        .o-tagnotice {
            &--important {
                flex-shrink: 0;
            }
            &--info {
                flex-shrink: 0;
            }
        }
        .o-notification {
            flex-grow: 1;
        }
        .o-updatedate {
            flex-shrink: 0;
        }
    }
    //--flexboxの指定 end --
}


/* 顧客一覧 */

.o-partnerlist {
    .o-list__row {
        height: 70px;
        .o-personalicon {
            margin: 0px 29px 0px 5px;
            background-color: $lightblue;
            color: $white;
        }
        .o-personaldetail {
            margin-right: auto;
            div:first-child {
                .o-settleinfo {
                    font-size: $font13;
                    color: $lightblue;
                    margin: 0px 5px;
                }
                .o-telno {
                    font-size: $font13;
                    color: $lightgrey;
                    margin: 0px 5px;
                }
            }
            div:last-child {
                flex-basis: auto;
                font-size: $font15;
                margin: 0px 5px;
            }
        }
        .o-tagservicecont {
            background-color: $lightblue--light;
            margin: 0 auto;
            margin: 0px 5px;
        }
        .o-messageicon {
            margin: 0px 5px;
            color: $darkgrey;
            &>.material-icons {
                vertical-align: middle;
            }
        }
        .o-updatedate {
            color: $darkgrey;
            margin: 0px 5px;
        }
        .o-menuicon {
            font-size: $font22;
            margin: 0px 5px;
        }
    }
    //--flexboxの指定 start --
    .o-rowcontainer {
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        .o-personalicon {
            flex-grow: 0;
            flex-shrink: 0;
        }
        .o-personaldetail {
            flex-grow: 1;
            justify-content: flex-start;
            div:first-child {
                display: flex;
                flex-direction: row;
                flex: 1;
                flex-basis: auto;
                .o-settleinfo {
                    flex: 1;
                    flex-basis: auto;
                }
                .o-telno {
                    flex: 10;
                    flex-basis: auto;
                }
            }
            div:last-child {
                flex: 1;
                flex-basis: auto;
            }
        }
        .o-personalicon {
            flex-grow: 0;
            flex-shrink: 0;
        }
    }
    //--flexboxの指定 end --
}

////////// Phone //////////
@media (max-width: $grid-tablet-breakpoint - 1) {
    .o-productlist.mdl-data-table .o-rowcontainer {
        padding-left: 12px;
        padding-right: 12px;
    }
    .o-noticelist.mdl-data-table .o-rowcontainer {
        padding-left: 12px;
        padding-right: 12px;
    }    
    .o-productlist {
        & .o-rowcontainer {
            flex-direction: column;
            & .o-productdetail {
                min-width: inherit;
                margin: 12px auto 0px 0px;
            }
            & .o-productstart {
                margin-top: 12px;
                margin-right: auto;
                margin-left: auto;
            }
        }
    }
    .o-noticelist {
        & .o-rowcontainer {
            flex-wrap: wrap;
            & .o-tagnotice {
                &--important {
                    order: 1;
                }
                &--info {
                    order: 1;
                }
            }
            & .o-notification {
                order: 3;
                width: 100%;
            }
            & .o-updatedate {
                order: 2;
                margin-left: auto;
                margin-right: 8px;
            }
            & .o-opendetail{
                order: 4;
                margin-right: 15px;
            }               
        }
        & .o-rowcontainer__detail {
            padding-right: 0px;
            padding-left: 0px;
        }
    }
}

